<template>
<div>
    <b-editable-table bordered class="editable-table" v-model="items" :fields="fields" @input-change="handleInput">
      <template #cell(isActive)="data">
        <span v-if="data.value">Yes</span>
        <span v-else>No</span>
      </template>
    </b-editable-table>
</div>
</template>

<script>
import BEditableTable from 'bootstrap-vue-editable-table'
// import { BButton } from 'bootstrap-vue'
import axios from 'axios'
export default {
  components: {
    BEditableTable
  },
  data () {
    return {
      monthData: {},
      fields: [
        { key: 'id', label: 'Id', type: 'text', editable: false, placeholder: '', class: 'name-col' },
        { key: 'month', label: 'Month', type: 'text', editable: true, placeholder: 'Enter Month...', class: 'name-col' },
        { key: 'monthName', label: 'Month Name', type: 'text', editable: true, placeholder: 'Enter Name...', class: 'name-col' },
        { key: 'year', label: 'Year', type: 'text', editable: true, placeholder: 'Enter Name...', class: 'name-col' },
        { key: 'days', label: 'No. of Days', type: 'text', editable: true, placeholder: 'Enter Name...', class: 'name-col' },
        {
          key: 'status',
          label: 'Status',
          type: 'select',
          editable: true,
          placeholder: 'Enter Name...',
          class: 'name-col',
          options: [
            { value: 'closed', text: 'closed' },
            { value: 'current', text: 'current' }
          ]
        }

        // {
        //   key: 'department',
        //   label: 'Department',
        //   type: 'select',
        //   editable: true,
        //   class: 'department-col',
        //   options: [
        //     { value: 1, text: 'HR' },
        //     { value: 2, text: 'Engineer' },
        //     { value: 3, text: 'VP' },
        //     { value: 4, text: 'CEO' }
        //   ]
        // },
        // { key: 'age', label: 'Age', type: 'range', min: '0', max: '100', editable: true, placeholder: 'Enter Age...', class: 'age-col' },
        // {
        //   key: 'dateOfBirth',
        //   label: 'Date Of Birth',
        //   type: 'date',
        //   editable: true,
        //   class: 'date-col',
        //   locale: 'en',
        //   'date-format-options': {
        //     year: 'numeric',
        //     month: 'numeric',
        //     day: 'numeric'
        //   }
        // },
        // { key: 'isActive', label: 'Is Active', type: 'checkbox', editable: true, class: 'is-active-col' }
      ],
      items: []
    }
  },
  mounted () {
    this.fetchMonthData()
  },
  methods: {
    async handleInput (value, data) {
      // console.log(value.item.year)
      // console.log(value)
      this.monthData = {
        id: value.item.id,
        month: value.item.month,
        monthName: value.item.monthName,
        year: value.item.year,
        days: value.item.days,
        status: value.item.status

      }
      const url = process.env.VUE_APP_API_URL + '/month'
      await axios
        .post(url, this.monthData, {
          headers: {
            'Content-Type': 'application/json'
          }
        })
        .then(response => {
          this.msg = {}
          //   commit('setMessage', 'Sucess')
          this.isBusy = !this.isBusy
          this.$toasted.show('Month data updated sucessfully!', {
            theme: 'bubble',
            position: 'top-right',
            duration: 5000
          })
        })
        .catch(error => {
          console.log(error.response.data)
          this.msg = 'Error:' + JSON.stringify(error.response.data.errorMessage)
        })
    },
    async fetchMonthData () {
      // alert(id)
      const url = process.env.VUE_APP_API_URL + '/month'
      await axios
        .get(url)
        .then((response) => {
          return response
        })
        .then((response) => {
          this.items = response.data
          this.msg = {}
        })
        .catch(error => {
          console.log(error.response.data)
          this.msg = error.response.data.errorMessage
        })
    }
  }
}
</script>

<style>
table.editable-table {
  margin: auto;
}

table.editable-table td {
  vertical-align: middle;
}

.editable-table .data-cell {
  padding: 8px;
  vertical-align: middle;
}

.editable-table .custom-checkbox {
  width: 50px;
}

.name-col {
  width: 120px;
}

.department-col {
  width: 150px;
}

.age-col {
  width: 100px;
}

.date-col {
  width: 200px;
}

.is-active-col {
  width: 100px
}
</style>
